import React from "react"
import { Link, graphql } from "gatsby"
import posed from "react-pose"
import Layout from "../components/layout"
import styles from "../styles/blog-post.module.css"
import closeSVG from "../images/close.svg"

const Box = posed.div({
  closed: { y: "300px" },
  open: { y: "0px", transition: { duration: 600 } },
})

const Back = posed.div({
  hoverable: true,
  pressable: true,
  init: {
    scale: 1,
  },
  hover: {
    scale: 1.2,
  },
  press: {
    scale: 1.1,
  },
})

export default ({ data }) => {
  const post = data.markdownRemark
  return (
    <Layout>
      <Box className="container" initialPose="closed" pose="open">
        <div className="headerOther">
          <Back>
            <Link to={`/${post.frontmatter.type}`}>
              <img src={closeSVG} alt="back" height="32" width="32" />
            </Link>
          </Back>
          <h2 className="center">{post.frontmatter.title}</h2>
        </div>
        <div>
          <div
            className={styles.blogtext}
            dangerouslySetInnerHTML={{ __html: post.html }}
          />
        </div>
        <div className="headerOther">
          <Link to={`/${post.frontmatter.type}`}>
            <img src={closeSVG} alt="back" height="32" width="32" />
          </Link>
        </div>
      </Box>
    </Layout>
  )
}

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        type
      }
    }
  }
`
